/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.errorScreen {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: #fff;
  min-height: 300px;
  padding: 100px;
  text-align: center;
  z-index: 10; }
  .errorScreen h4 {
    margin: 30px 0;
    font-size: 20px; }
  .errorScreen .errorLogo img {
    height: 50px; }
  .errorScreen p {
    width: 500px;
    margin: auto; }
  .errorScreen textarea {
    border: 1px solid #ddd;
    width: 500px !important;
    padding: 10px !important;
    height: 200px !important;
    margin: 0 auto; }
