/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

#footer {
  font-size: 12px;
  border-top: 1px solid #ddd;
  background: #f5f5f5; }
  #footer .footer-layout {
    display: flex;
    text-align: left;
    justify-content: space-between;
    max-width: 1024px;
    margin: auto; }
    #footer .footer-layout a {
      text-decoration: none;
      color: #333; }
    #footer .footer-layout h3 {
      color: #666 !important;
      font-size: 15px; }
    #footer .footer-layout ul {
      line-height: 20px;
      color: #666 !important;
      list-style: none;
      padding-left: 0; }
